import * as React from "react";
import { useDispatch, useSelector } from "react-redux";
import { ActionCreator } from "../../store/action";
var AmountFilter = function () {
    var phones = useSelector(function (state) { return state.phones; });
    var amount = useSelector(function (state) { return state.phonesAmount; });
    var dispatch = useDispatch();
    var buttons = [];
    if (phones.length > 1) {
        for (var i = 2; i <= phones.length && i <= 6; i++) {
            buttons.push(i);
        }
    }
    var onButtonClick = function (evt) {
        var element = evt.target;
        if (Number(element.textContent) === amount) {
            return;
        }
        dispatch(ActionCreator.changePhonesAmount(Number(element.textContent)));
    };
    return (phones.length > 1
        ? React.createElement("div", { className: "amount-filter" },
            React.createElement("p", { className: "amount-filter__text" }, "\u041E\u0442\u043E\u0431\u0440\u0430\u0437\u0438\u0442\u044C \u0442\u043E\u0432\u0430\u0440\u044B:"),
            buttons.map(function (button, i) {
                return React.createElement("button", { key: "button-".concat(i), className: "amount-filter__button".concat(button === amount ? " active" : ""), disabled: button === amount, onClick: function (evt) { return onButtonClick(evt); } }, button);
            }))
        : React.createElement("div", null));
};
export default AmountFilter;
